import clsx from "clsx";
import React, { useState } from "react";
import "../../styles/tailwind.css";

export interface ButtonProps {
  backgroundColor?: string;
  width?: number | string;
  height?: number | string;
  label: string;
  onClick?: () => void;
  rounded?: boolean;
  className?: string;
  color?: string;
  hoverColor?: string;
  type?: "fullcolor" | "text" | "outline";
}

/** Primary UI component for user interaction */
export const Button = ({
  width,
  height,
  backgroundColor,
  label,
  rounded,
  className,
  color,
  hoverColor,
  type,
  ...props
}: ButtonProps) => {
  const [hover, setHover] = useState(false);

  return (
    <button
      onMouseOver={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
      type="button"
      className={`${className} flex items-center justify-center border-solid rounded-lg border-2 px-3 py-1 border-main-color text-main-color font-bold ${clsx(
        {
          // fullColor === true
          [`text-white bg-main-color`]:
            (!color && hover) || type === "fullcolor",
          [`bg-base-color border-base-color`]:
            !color && hover && type === "fullcolor",
          // color === true
          [`text-white bg-[${color}]`]: !type && color && hover,
          [`text-[${color}]`]: color,
          [`text-white bg-${color}`]: color && type === "fullcolor",
          [`border-${color} border-[${color}]`]:
            color && type === "fullcolor" && hover,
          // hoverColor === true
          [`bg-[${hoverColor}]`]: hoverColor,
          // rounded === true
          ["!rounded-3xl"]: rounded,
          // type === text
          [`bg-transparent border-0 text-main-color`]: type === "text",
          [`text-base-color`]: type === "text" && hover,
          [`text-[${color}]`]: type === "text" && color,
          // type === outline
          [`bg-transparent text-main-color`]: type === "outline",
          [`text-[${color}] border-[${color}]`]: type === "outline" && color,
          [`text-base-color border-base-color`]: type === "outline" && hover,
        }
      )}`}
      style={{
        backgroundColor,
        width: width,
        height: height,
        WebkitUserSelect: "none",
        MozUserSelect: "none",
        msUserSelect: "none",
        userSelect: "none",
      }}
      {...props}
    >
      {label}
    </button>
  );
};
